
/* Side Navigation Bar Container */
.navigation {
    display: flex;
    position: fixed; /* Change to fixed for a fixed position */
    top: 0;
    left: 0;
    z-index: 5;
    flex-direction: column;
    height: 100%;
    padding: 15px;
    background-color: rgba(251, 252, 253, 0.95); /* Adjust background color alpha for better visibility */
    box-shadow: 2px 0px 5px rgba(0, 0, 0, 0.2); /* Adjust box shadow for better visibility */
    border-radius: 5px;
  }
  
  .icon img {
    width: 120px; /* Slightly reduce the logo size */
    height: 40px; /* Slightly reduce the logo size */
  }
  
  /* Bars Icon Styling */
  .icon {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 20px; /* Add some space between bars icon and navigation tabs */
  }
  
  .icon .fa-bars, .fa-close {
    cursor: pointer;
    font-size: 24px;
  }
  .fa-bar:hover, .fa-close:hover{
    color: orange;
  }
  
  /* Navigation Tabs Styling */
  .tabs {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  
  .tabs a {
    text-decoration: none;
    color: black;
    font-weight: 700;
    margin: 15px 0;
    font-size: 16px; /* Adjust font size for better visibility */
    transition: color 0.3s ease;
  }
  
  .tabs a:hover {
    color: rgb(0, 80, 123);
  }
    
